* {
  margin: auto;
}

h1 span {
  font-family: Montserrat;
  font-size: 32px;
  font-style: italic;
  font-weight: 700;
  line-height: 48px;
  text-align: left;
}

h1 {
  font-family: Montserrat;
  font-size: 32px;
  font-style: italic;
  font-weight: 500;
  line-height: 48px;
  text-align: left;
  color: white;
}

.leftPartContainer {
  max-width: 751px;
}

.mensIcon {
  max-width: 854px;
}

.containerHeaderContent {
  display: flex;
  justify-content: center;
  align-items: center;
  padding-inline: 20px;
  flex-wrap: wrap-reverse;
}

.btnHTSHeader {
  width: 493px;
  height: 96px;
  background: linear-gradient(
    225deg,
    #3d4eb0 0%,
    #4758b6 20.5%,
    #4959b7 24%,
    #3a4aa9 52.52%,
    #4b5bb5 74.5%,
    #4556b6 100%
  );

  box-shadow: 0px 4px 4px 0px #00000040;
  color: white;

  font-family: Montserrat;
  font-size: 24px;
  font-weight: 700;
  line-height: 24.38px;
  text-align: center;
  text-transform: none;
  margin-block-start: 60px;
}

.containerHeader {
  background-image: linear-gradient(
      44.71deg,
      rgba(0, 0, 1, 1) 0%,
      rgba(29, 57, 82, 1) 48.851%,
      rgba(12, 41, 64, 1) 100.724%
    ),
    url("../../../images/bgheader.png");
  padding-block-start: 170px;
}

.containerRecomended {
  background: linear-gradient(
      225deg,
      #000000 0%,
      #193a57 0%,
      #1d3952fb 51.19%,
      #0c2940f8 99.73%
    ),
    url("../../../images/401cfb2e26b2108ca6ee75238f23a8e1.png");

  text-align: center;
  color: white;
}

h3 {
  font-family: Montserrat;
  font-size: 36px;
  font-weight: 700;
  line-height: 43.88px;
  padding-block-start: 108px;
}

.containerStars {
  max-width: 1519px;
  display: flex;
  flex-wrap: wrap;
  margin-block-start: 50px;
  padding-block-end: 108px;
  align-items: center;
  align-self: center;
  align-content: center;
  text-align: center;
  justify-content: center;
  margin-inline: auto;
  gap: 10px;
}

.blockStar {
  max-width: 493px;
  width: 100%;
  max-height: 219px;
  height: 219px;
  background: #00000031;
  gap: 0px;
  display: flex;
  flex-direction: column;
  justify-content: center;

  align-items: center;
  align-self: center;
  align-content: center;
  text-align: center;
  justify-content: center;
  margin-inline: 0;
}

.blockStar p {
  font-family: Montserrat;
  font-size: 24px;
  font-weight: 500;
  line-height: 29.26px;
  text-align: center;
  max-width: 250px;
  margin-block-start: 0;
}

.blockStar img {
  width: 60px;
  height: 60px;
  margin-block-start: 50px;
}

@media (max-width: 1300px) {
  .leftPartContainer h1 {
    font-size: 20px;
    max-width: 500px;
    line-height: 30px;
  }

  .btnHTSHeader {
    width: 293px;
    height: 66px;
  }
}

@media (max-width: 1047px) {
  .containerHeaderContent {
    position: relative;
    max-width: 100vw;
  }

  .mensContainer,
  .mensIcon {
    max-width: 100vw;
  }

  .leftPartContainer {
    min-width: 100vw;
    background: #17171782;
    text-align: center;
    position: absolute;
    bottom: 0;
    max-width: 100vw;
  }

  .leftPartContainer h1 {
    text-align: center;
    padding: 25px;
    font-size: 18px;
    line-height: 30px;
  }

  .leftPartContainer span {
    font-size: 18px;
    line-height: 30px;
  }

  .leftPartContainer button {
    margin-block-start: 0;
    margin-block-end: 38px;
    max-width: 296px;
    height: 57px;
    font-size: 15px;
  }
}
